<template>
  <!-- @click  点击 => 播放  -->
  <div class="trackSongList" @click="$emit('translate-song', trackSongs)">
    <!-- 是否显示数字 -->
    <template v-if="news">
      <span class="order" v-if="order !== undefined">{{cngNum(order + 1)}}</span>
      <div class="main" :style="{'margin-left':order!==undefined?'0px':'15px'}">
        <h3>
          <span>{{trackSongs.name}}</span>
          <span v-if="trackSongs.song.alias.length!=0" class="alias">({{trackSongs.song.alias[0]}})</span>
        </h3>
        <div class="info">
          <i class="icon icon-quality"></i>
          <span>{{artistConnect(trackSongs.song.artists)}}</span>
          <span class="album">-{{trackSongs.song.artists.name}}</span>
        </div>
      </div>
      <img class="icon icon-play" style="background:none" v-if="currentSong && trackSongs.id === currentSong.id"
        src="../assets/playing.svg" alt />
      <i v-else class="icon icon-play"></i>
    </template>

    <template v-else>
      <span class="order" v-if="order !== undefined" :class="{active:order<3?true:false}">{{cngNum(order + 1)}}</span>
      <div class="main">
        <h3>
          <span>{{trackSongs.name}}</span>
          <span v-if="trackSongs.alia.length!=0" class="alias">({{trackSongs.alia[0]}})</span>
        </h3>
        <div class="info">
          <i class="icon icon-quality"></i>
          <span>{{artistConnect(trackSongs.ar)}}</span>
          <span class="album">-{{trackSongs.al.name}}</span>
        </div>
      </div>
      <img class="icon icon-play" style="background:none" v-if="currentSong && trackSongs.id === currentSong.id"
        src="../assets/playing.svg" alt />
      <i v-else class="icon icon-play"></i>
    </template>

  </div>
</template>

<script>
  export default {
    name: "track-songs",
    props: {
      order: Number,
      trackSongs: Object,
     news:Boolean,
      currentSong: Object
    },
    methods: {
      // 艺术家/
      artistConnect(artists) {
        return artists.map(artists => artists.name).join(" / ");
      },
      cngNum(n) {
        return n < 10 ? "0" + n : n;
      },
    },

  }
</script>

<style lang="less" scoped>
  .icon {
    background-image: url("../assets/index_icon_2x.png");
    display: inline-block;
    background-size: 166px auto;

    &.icon-quality {
      width: 12px;
      height: 8px;
      margin: 0 5px 0 2px;
    }

    &.icon-play {
      width: 25px;
      height: 25px;
      background-position-x: -23px;
      margin: 0 10px;
    }
  }

  .trackSongList {
    display: flex;
    padding: 5px 0;
    border-bottom: 1px solid rgba(189, 189, 189, 0.2);
    align-items: center;

    .order {
      font-size: 16px;
      width: 2em;
      text-align: center;
      color: #999;
      margin-left: 5px;

      &.active {
        color: #df3436;
      }
    }

    .main {
      flex: 1;
      width: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      h3 {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .alias,
      .info {
        color: #888;
      }

      .info {
        font-size: 12px;
        line-height: 2em;
      }
    }

  }
</style>