<template>
  <div class="comments">
    <header>
      <i class="fa fa-angle-left" @click="$emit('showComList',false)"></i>
      <h3>评论({{comments.total}})</h3>
      <img :src="currentSong.picUrl?currentSong.picUrl:currentSong.al.picUrl" alt="">
    </header>
    <div class="control">
      <span>按{{showDeff}}排序</span>
      <div class="sort">
        <span @click="showSort=!showSort">{{showDeff}}<i class="fa fa-caret-down"></i></span>
        <ul v-show="showSort">
          <li @click="cngList(1)">最新</li>
          <li @click="cngList(2)">热门</li>
        </ul>
      </div>
    </div>
    <!-- 头像 comments.hotComments/comments =>user.avatarUrl -->
    <!-- 评论时间 comments.hotComments/comments =>time -->
    <!-- 点赞数量  comments.hotComments/comments =>likedCount-->
    <!-- 评论者名字 comments.hotComments/comments =>nickname -->
    <main>
      <template v-if="showLeast">
        <ul class="least" v-for="(item,index) in comments.comments" :key="index">
          <li class="commentList">
            <div class="lt">
              <img :src="item.user.avatarUrl" alt="">
            </div>
            <div class="rt">
              <div class="header">
                <div class="left">
                  <h3>{{item.user.nickname}}</h3>
                  <span>{{cngTime(item.time)}}</span>
                </div>
                <div class="right">{{item.likedCount}}<i class="fa fa-thumbs-o-up"></i></div>
              </div>
              <div class="main">{{item.content}}</div>
            </div>
          </li>
        </ul>
      </template>
      <template v-else>
        <ul class="hot" v-for="(item,index) in comments.hotComments" :key="index">
          <li class="commentList">
            <div class="lt">
              <img :src="item.user.avatarUrl" alt="">
            </div>
            <div class="rt">
              <div class="header">
                <div class="left">
                  <h3>{{item.user.nickname}}</h3>
                  <span>{{cngTime(item.time)}}</span>
                </div>
                <div class="right">{{item.likedCount}}<i class="fa fa-thumbs-o-up"></i></div>
              </div>
              <div class="main">{{item.content}}</div>
            </div>
          </li>
        </ul>
      </template>
    </main>
  </div>
</template>

<script>
  export default {
    name: "SongComments",
    props: {
      comments: Object,
      currentSong: Object,
    },
    data() {
      return {
        showSort: false,
        showDeff: '最新',
        showLeast: true,
      }
    },

    methods: {
      cngTime(time) {
        let date = new Date(time);
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        let d = date.getDate();
        let h = date.getHours();
        let min = date.getMinutes();
        let s = date.getSeconds();
        h = h < 10 ? '0' + h : h;
        min = min < 10 ? '0' + min : min;
        s = s < 10 ? '0' + s : s;
        return y + '年' + m + '月' + d + '日' + h + '时' + min + '分' + s
      },
      cngList(v) {
        v == 1 ? this.showDeff = "最新" : this.showDeff = "热门";
        v == 1 ? this.showLeast =  true: this.showLeast = false;
        this.showSort = false;
      }
    },
  }
</script>

<style lang="less" scoped>
  .flex() {
    display: flex;
  }

  .comments {
    background-color: white;
    z-index: 99;
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 60vh;
    border-radius: 10px 10px 0 0;
    overflow-y: scroll;

    header {
      text-align: center;
      .flex();
      font-size: 16px;
      margin: .5em 0;

      i,
      img {
        height: 20px;
        width: 20px;
        margin: 0 10px;
        font-size: 20px;
        border-radius: 50%;
      }

      h3 {
        flex: 1
      }

    }

    .control {
      .flex();
      padding: 0 10px;
      font-weight: 800;
      margin-bottom: .5em;
      justify-content: space-between;

      .sort {
        position: relative;
        text-indent: .5em;

        span {
          display: block;

          i {
            margin: 0 5px;
          }
        }

        ul {
          position: absolute;
          top: 20px;
          background: white;
          width: 100%;
          border: 1px solid #ccc;
        }
      }
    }

    main {
      padding: 0 10px;

      ul {
        .commentList {
          padding: 5px 0;
          .flex();
          width: 100%;

          .lt {
            margin: 0 5px;

            img {
              border-radius: 50%;
              height: 30px;
              width: 30px;
              vertical-align: middle;
            }
          }

          .rt {
            flex: 1;
            padding-bottom: 10px;
            border-bottom: 1px solid lightgray;

            .header {
              .flex();
              justify-content: space-between;
              margin-bottom: 5px;

              .left {
                line-height: 1em;

                h3 {
                  color: #888;
                }

                span {
                  font-size: 12px;
                  color: lightgray;
                }
              }

              .right {
                i {
                  margin: 0 5px;
                }
              }
            }
          }
        }
      }
    }
  }
</style>