<template>
  <div class="musicPlayList">
    <!-- 底部播放器 -->
    <div class="musicPlayBottom" @click.stop="showSongDetail=true">
      <div class="cover">
        <img :src="currentSong.picUrl?currentSong.picUrl:currentSong.al.picUrl" :class="{active:this.playing}"
          class="thumb" />
      </div>
      <div class="song">
        <h5>{{currentSong.name}}</h5>
        <span>{{cngArtist(currentSong.ar||currentSong.song.artists)}}</span>
      </div>
      <div class="status" @click.stop="playing=!playing" style="width:30px;height:30px">
        <canvas id="canvas" width="30" height="30"></canvas>
        <i v-if="playing" class="fa fa-pause"></i>
        <i v-else class="fa fa-play"></i>
      </div>
      <div class="list" @click.stop="showList=!showList">
        <i class="fa fa-bars"></i>
      </div>
    </div>
    <!-- 播放列表 -->
    <transition name="custom-classes-transition" enter-active-class="animated slideInUp"
      leave-active-class="animated slideOutDown">
      <ul v-if="showList" class="playList">
        <li v-for="(item,index) in playList" :key="index">
          <track-songs @translate-song="$emit('translate-song', $event)" :currentSong="currentSong" :trackSongs="item"
            :news="item.news" :order="index">
          </track-songs>
        </li>
      </ul>
    </transition>

    <!-- 组件songdetail -->
    <transition name="custom-classes-transition" enter-active-class="animated fadeInUp"
      leave-active-class="animated fadeOutDown">
      <div class="songDetail" v-show="showSongDetail">
        <div class="black"></div>
        <div class="mask"
          :style="{'backgroundImage':`url(${currentSong.picUrl?currentSong.picUrl:currentSong.al.picUrl})`}">
        </div>
        <!-- 返回+分享 -->
        <header>
          <div class="back" @click="showSongDetail=false"><i class="fa fa-angle-left"></i></div>
          <div class="info">
            <h3>{{currentSong.name}}</h3>
            <span>{{cngArtist(currentSong.ar||currentSong.song.artists)}}</span>
          </div>
          <div class="share"><i class="fa fa-share-alt-square"></i></div>
        </header>
        <!-- 黑胶唱片+歌词 -->
        <main :class="{active:playing}">
          <div class="glue" v-show="showCircle" @click="showCircle = false">
            <div class="needle"></div>
            <div class="vinyl" @click="showAllLyric=true">
              <img :src="currentSong.picUrl?currentSong.picUrl:currentSong.al.picUrl">
            </div>
          </div>
          <div class="lyric" v-show="!showCircle" @click="showCircle = true">
            <ul class="scroll" :style="{'transform': `translateY(${-currentLyricIndex * 28}px)`}">
              <li v-for="(item, index) in filterLyric" :class="{active: index===currentLyricIndex}" :key="index">
                {{item.text}}</li>
            </ul>
          </div>
        </main>
        <!-- 底部播放工具 -->
        <footer>
          <div class="tools">
            <div @click="loving=!loving">
              <i :style="{'color':loving?'red':'white'}" class="fa fa-heart"></i>
            </div>
            <i class="fa fa-comments" @click="showComments='true'"></i>
            <i class="fa fa-ellipsis-v"></i>
          </div>
          <div class="progress">
            <span>{{startTime}}</span>
            <div class="range">
              <input type="range" class="progress-range" value="circleWidth" min="0" step="0.01" max="100"
                @input="rangeDrag()" />
              <div class="circle"></div>
            </div>
            <span>{{songDuration}}</span>
          </div>
          <div class="status">
            <i class="fa fa-step-backward" @click="cngSongPlay(prevSong)"></i>
            <div @click.stop="playing=!playing">
              <i v-if="playing" class="fa fa-pause"></i>
              <i v-else class="fa fa-play"></i>
            </div>
            <i class="fa fa-step-forward" @click="cngSongPlay(nextSong)"></i>
          </div>
        </footer>
        <!-- 组件 comments -->
        <transition name="custom-classes-transition" enter-active-class="animated slideInUp"
          leave-active-class="animated slideOutDown">
          <SongComments v-show="showComments" :comments="songComments" :currentSong="currentSong"
            @showComList="showComments=$event"></SongComments>
        </transition>
      </div>
    </transition>
    <!-- 音乐控件 -->
    <audio :src="currentSongUrl" controls autoplay style="height:0"></audio>
  </div>

</template>

<script>
  import TrackSongs from "@/components/TrackSongs.vue"
  import SongComments from "@/components/SongComments.vue";

  export default {
    name: "PlaySongList",
    data() {
      return {
        playing: true,
        showList: false,
        playList: [],
        showSongDetail: false,
        showLyric: null,
        loving: false,
        startTime: null,
        songDuration: null,
        circleWidth: null,
        showCircle: true,
        currentLyricIndex: 0,
        songComments: null,
        showComments: false,
        currentSongIndex: 0
      }
    },
    components: {
      TrackSongs,
      SongComments
    },
    props: {
      currentSong: Object
    },
    methods: {
      // 播放圈圈
      drowStatus() {
        var canvas = this.$el.querySelector('#canvas');
        var audio = this.$el.querySelector("audio");
        var duration = this.currentSong.dt ? this.currentSong.dt : this.currentSong.song.duration;
        audio.ontimeupdate = () => {
          /** @type {HTMLCanvasElement} */
          var context = canvas.getContext('2d');
          context.clearRect(0, 0, 30, 30);
          context.beginPath();
          context.arc(15, 15, 13, 0, Math.PI * 2);
          context.closePath();
          context.strokeStyle = 'lightgray';
          context.lineWidth = "1";
          context.stroke();
          context.beginPath();
          context.arc(
            15,
            15,
            13,
            -0.5 * Math.PI,
            -0.5 * Math.PI + 2 * Math.PI * ((audio.currentTime * 1000) / duration)
          );
          context.strokeStyle = "rgba(255,0,0,0.7)";
          // 描边样式
          context.lineWidth = "1";
          // 描边宽度
          context.stroke(); // 路径描边
          // console.log(audio.currentTime / audio.duration);
          let precent = (audio.currentTime / audio.duration) * 100;
          this.circleWidth = Math.floor(precent) + '%';
          // 时间
          this.startTime = this.timeCng(audio.currentTime);
          // 进度条改变
          this.$el.querySelector('.range input').value = precent;
          this.$el.querySelector('.range .circle').style.width = precent + '%';
          // 歌词滚动
          let index;
          for (let i = 0; i < this.filterLyric.length; i++) {
            if (audio.currentTime + 0.15 < this.filterLyric[i].time) {
              index = i - 1;
              break;
            }
          }
          if (index === undefined) {
            index = this.filterLyric.length - 1;
          }
          this.currentLyricIndex = index;
        }
      },
      cngArtist(v) {
        return v.map(v => v.name).join("/");
      },
      // 获取歌词
      getLyric() {
        var hasLyric = window.localStorage.getItem(
          "lyric" + this.currentSong.id
        );
        if (hasLyric) {
          this.showLyric = hasLyric;
        } else {
          window.axios
            .get("lyric", {
              params: {
                id: this.currentSong.id
              }
            })
            .then(response => {
              if (response.data.lrc != undefined) {
                this.showLyric = response.data.lrc.lyric;
                window.localStorage.setItem(
                  "lyric" + this.currentSong.id,
                  response.data.lrc.lyric
                );
              } else {
                this.showList = null
              }
            });
        }
      },
      // 获取歌词评论
      getComment() {
        let hasComment = JSON.parse(window.localStorage.getItem('comments' + this.currentSong.id));
        if (hasComment && hasComment.expire > Date.now()) {
          this.songComments = hasComment.result;
        } else {
          window.axios.get('/comment/music', {
            params: {
              id: this.currentSong.id
            }
          }).then(response => {
            // console.log(response.data);
            this.songComments = response.data
            window.localStorage.setItem('comments' + this.currentSong.id, JSON.stringify({
              expire: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data
            }));
          })
        }
      },
      // 时间计算
      timeCng(v) {
        let m = parseInt((v / 60) % 60);
        let s = parseInt(v % 60);
        // console.log(m, s);
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return m + ':' + s;
      },
      // 进度条拖拽
      rangeDrag() {
        let audio = this.$el.querySelector('audio');
        audio.currentTime = (this.$el.querySelector('.range input').value * audio.duration) / 100;
      },
      cngSongPlay(song) {
        this.currentSong = this.playList[song];
        this.$emit('translate-song', this.playList[song]);
      }
    },
    computed: {
      currentSongUrl: function () {
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      },
      // 过滤筛选歌词
      filterLyric: function () {
        if (this.showLyric != null) {
          // console.log(this.showLyric);
          return this.showLyric.split('\n').map(item => {
            // console.log(item);
            // 正则筛选
            let regExp = /\d{2}:\d{2}\.\d+/i;
            if (item.search(regExp) !== -1) {
              let time = item.match(regExp)[0];
              let m = time.substr(0, 2);
              let s = time.substr(3, 2);
              let n = time.substr(5);
              return {
                time: Number(m) * 60 + Number(s) + Number(n),
                text: item.substr(11) || "---------"
              };
            } else {
              return {}
            }
          })
        } else {
          return '暂时未有歌词';
        }
      },
      prevSong: function () {
        return this.currentSongIndex == 0 ? this.playList.length - 1 : this.currentSongIndex - 1;
      },
      nextSong: function () {
        return this.currentSongIndex == this.playList.length - 1 ? 0 : this.currentSongIndex + 1;
      }
    },
    watch: {
      // 监听playing状态
      playing: function (value) {
        var audio = this.$el.querySelector("audio");
        if (value) {
          audio.play();
        } else {
          audio.pause();
        }
      },
      //监听 currentSong是否更换
      currentSong: function (value) {
        // console.log(value);
        // 检测是否存放了相同数据
        this.getLyric();
        this.getComment();
        let hasExist = this.playList.some(item => {
          this.currentSongIndex = this.playList.indexOf(this.currentSong);
          return item.id == value.id;
        })
        if (!hasExist) {
          value.song != undefined ? value.news = true : value.news = false;
          this.playList.push(value);
          this.currentSongIndex = this.playList.length - 1;
          // console.log(this.playList);
        }
      },
    },
    created() {
      // 创建时传入第一个数据
      this.getLyric();
      this.getComment();
      this.currentSong.song != undefined ? this.currentSong.news = true : this.currentSong.news = false;
      this.playList.push(this.currentSong);
      this.songDuration = this.timeCng((this.currentSong.dt ? this.currentSong.dt : this.currentSong.song.duration) /
        1000);
    },
    mounted() {
      this.drowStatus();
      // 箭头函数没有this指向 所以this仍指向组件实例
      var audio = this.$el.querySelector('audio');
      // 播放
      audio.onplay = () => {
        this.playing = true;
      };
      // 暂停
      audio.onpause = () => {
        this.playing = false;
      };
    }
  };
</script>

<style lang="less" scoped>
  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }

  .musicPlayList {
    .musicPlayBottom {
      bottom: 0;
      left: 0;
      z-index: 10;
      position: fixed;
      background: white;
      border-top: 1px solid lightgray;
      width: 100%;
      display: flex;
      align-items: center;
      padding: 5px 0;
      font-size: 12px;
      height: 50px;

      .cover {
        .thumb {
          height: 36px;
          width: 36px;
          border-radius: 50%;
          animation: rotate 6s linear infinite;
          animation-play-state: paused;
          transform: rotate(0deg);
          margin: 0 10px;

          &.active {
            animation-play-state: running;
          }
        }
      }

      .song {
        flex: 1;
        line-height: 18px;

        span {
          color: #888;
        }
      }

      .status {
        position: relative;

        #canvas {
          position: absolute;
          left: 0;
          top: 0;
        }
      }

      .status,
      .list {
        margin: 0 10px;
        width: 28px;
        height: 28px;
        cursor: pointer;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .list {
        border: 1px solid #ccc;
        border-radius: 50%;
      }
    }

    .playList {
      position: fixed;
      bottom: 50px;
      left: 5%;
      width: 90%;
      background: white;
      border-radius: 10px 10px 0 0;
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3);
      z-index: 9;
      padding: 5px 5px 0;

      h5 {
        text-align: center;
        padding: 10px 0;
      }

      .trackSongList {
        padding: 0;
      }
    }

    .songDetail {
      display: flex;
      flex-direction: column;
      height: 100vh;
      width: 100%;
      z-index: 10;
      position: fixed;
      top: 0;
      left: 0;

      .black {
        width: 100%;
        height: 100%;
        background-color: black;
        position: absolute;
        z-index: -2;
      }

      .mask {
        filter: blur(30px) brightness(.5);
        transform: scale(2);
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-color: rgba(0, 0, 0, 0.3);
        position: absolute;
        z-index: -1;
      }

      header {
        display: flex;
        color: white;
        align-items: center;
        text-align: center;
        height: 50px;

        .back,
        .share {
          font-size: 30px;
          margin: 0 20px;
        }

        .back {
          font-size: 40px;
        }

        .info {
          flex: 1;

          h3 {
            font-size: 14px;
          }

          span {
            font-size: 12px;
          }
        }
      }

      main {
        text-align: center;
        position: relative;
        flex: 3;
        overflow: hidden;
        margin-top: 2em;

        .glue {
          .needle {
            background-image: url("../assets/needle.png");
            width: 96px;
            height: 137px;
            background-size: 100%;
            position: absolute;
            left: 50%;
            top: 0;
            z-index: 1;
            margin-left: -16px;
            transform: rotate(-12deg);
            transform-origin: 16px 0;
            transition: all 0.3s;
          }

          .vinyl {
            padding: 54px;
            background-image: url("../assets/disc_light-ip6.png"),
              url("../assets/disc-ip6.png");
            background-size: 100%;
            border-radius: 50%;
            display: inline-block;
            animation: rotate 6s linear infinite;
            animation-play-state: paused;
            margin-top: 70px;
            position: relative;

            img {
              width: 150px;
              height: 150px;
              border-radius: 50%;
              vertical-align: middle;
            }

          }
        }


        // 播放时
        &.active {
          .needle {
            transform: rotate(0deg);
          }

          .vinyl {
            animation-play-state: running;

            .cover {
              animation-play-state: paused;
            }
          }
        }

        .lyric {
          flex: 1;
          position: absolute;
          width: 100%;
          height: 100%;
          top: 50%;

          .scroll {
            transition: transform 0.3s;

            li {
              line-height: 2em;
              font-size: 14px;
              height: 2em;
              color: rgb(138, 136, 136);

              &.active {
                color: white;
              }
            }
          }
        }
      }

      footer {
        flex: 1;
        color: white;

        i {
          font-size: 26px;
        }

        .tools,
        .status {
          display: flex;
          justify-content: space-around;
          margin: 20px 0;
        }

        .progress {
          display: flex;
          align-items: center;
          margin: 10px 0;

          span {
            margin: 0 7.5px;
          }

          .range {
            flex: 1;
            width: 100%;
            background-color: rgba(90, 88, 88, 0.5);
            position: relative;
            height: 5px;

            input {
              position: absolute;
              z-index: 3;
              cursor: pointer;
              width: 100%;
              height: 100%;
              opacity: 0;
            }

            .circle {
              position: absolute;
              left: 0;
              background-color: white;
              height: 100%;

              &::after {
                position: absolute;
                background-color: white;
                content: "";
                width: 10px;
                height: 10px;
                border-radius: 50%;
                top: 50%;
                margin-top: -5px;
                right: -5px;
              }
            }
          }
        }
      }
    }
  }
</style>